import { createStore } from "vuex";
import { router } from './router';

export default createStore({
    state: {
        lang: "ko",
        account: {
            mid: 0,
            email: "",
            server: "",
            resource: 0,
            login: false,
        },
        page: {
            name: null,
            seq: 1,
            maxSeq: 1,
            nid: "root",
        },
        refs: {
            console: null,
            webview: null
        },
        toggle: {
            tag: true,
            mobile: false
        },
        component: {
            categories: []
        },
    },
    mutations: {
        setRefs(state, refs) {
            state.refs = refs;
        },
        setPage(state, page) {
            state.page = page;
        },
        setToggle(state, payload) {
            if (payload.tag !== undefined) {
                state.toggle.tag = payload.tag;
            }

            if (payload.mobile !== undefined) {
                state.toggle.mobile = payload.mobile;
            }
        },
        setComponent(state, payload) {
            if (payload.categories !== undefined) {
                state.component.categories = payload.categories;
            }
        },
        setLang(state, payload) {
            const metas = [];

            if (state.lang !== payload) {
                let replacePath = "";
                state.lang = payload;

                if (location.pathname) {
                    const arr = location.pathname.substring(1).split("/");
                    arr[0] = payload;
                    replacePath = "/" + arr.join("/");
                }
                else {
                    replacePath = "/" + payload;
                }

                router.replace(replacePath + location.hash);
            }

            document.title = payload == "ko" ? process.env.VUE_APP_TITLE_KO : process.env.VUE_APP_TITLE_EN;
            document.documentElement.setAttribute("lang", payload);
            metas.push(document.querySelector('meta[name="description"]'))
            metas.push(document.querySelector('meta[property="og:description"]'))
            metas.push(document.querySelector('meta[property="twitter:description"]'))

            for (let m of metas) {
                m.setAttribute("content", payload == "ko" ? process.env.VUE_APP_DESC_KO : process.env.VUE_APP_DESC_EN);
            }
        }
    }
})